.review {
    color: #fff;
    
    &__text {
        margin: 0 0 20px 0;
        padding: 0;
    }
    
    &__author {
        margin-right: 20px;
    }
    
    &__info {
        flex-shrink: 0;
        width: 60%;
    }
    
    &__image-col {
        width: 40%;
        margin-right: 25px;
    }
    
    &__image-wrap {
        width: 100%;
        padding-top: 100%;
        overflow: hidden;
        position: relative;
    }
    
    &__image {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}