.poster {
    padding: 65px 0;
    
    &__link {
        display: block;
    }
}

@media screen and (max-width: $phoneWidth) {
    .poster {
        padding: 40px 0;
        
        &__link {
            margin: 0 -16px;
        }
        
        &__img,
        source {
            width: 100%;
        }
    }
}