.social {
    flex-wrap: nowrap;
    
    &__list {
        margin: 0;
        padding: 0;
        list-style: none;
    }
    
    &__item {
        margin-right: 15px;
        
        &:last-of-type {
            margin-right: 0;
        }
    }
    
    &__link {
        display: block;
        width: 25px;
        height: 25px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: 20px;
        transition: opacity .4s;
        
        &--insta {
            background-image: url("../img/assets/icon-insta.svg");
        }
        
        &:hover {
            opacity: .75;
        }
    }
    
    &__promo {
        margin-right: 24px;
    }
}