.video-info {
    text-align: center;
    
    &__link {
        display: block;
        padding: 50px 10px;
        color: $color;
        text-decoration: none;
    }
    
    &__type {
        font-size: 13px;
        font-weight: bold;
        text-transform: uppercase;
    }
    
    &__descr {
        font-size: 25px;
        font-weight: bold;
        line-height: 1.3;
        margin: 40px 0;
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .video-info {
    
        &__link {
            padding: 40px;
        }
    
        &__descr {
            margin: 20px 0;
            color: #fff;
            font-size: 20px;
            line-height: 1.4;
    
            span {
                background-size: 100% 100%;
                background-image: linear-gradient($color, $color);
                background-repeat: no-repeat;
                transition: opacity .4s;
            }
        }
    }
}