@charset "UTF-8";

*,
*::before,
*::after {
  box-sizing: inherit;
}


html {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  font-family: $mainFont;
  color: $color;
  font-size: 17px;
  font-weight: normal;
  font-style: normal;
  line-height: 1.6;
  overflow-wrap: break-word;
  word-wrap: break-word;
  background-color: #FFF;

  @media screen and (max-width: $smDesktopWidth) {
    font-size: 16px;
  }

  @media screen and (max-width: $phoneWidth) {
    font-size: 15px;
  }

}

img {
  max-width: 100%;
  height: auto;
}

main {
  min-height: calc(100vh - 250px);
  
  @media screen and (max-width: $smDesktopWidth) {
    min-height: calc(100vh - 305px);
  }
}

.container-max {
  max-width: 1920px;
  min-width: 320px;
  padding: 0 16px;
  margin: 0 auto;
}

.container {
  max-width: 1200px;
  min-width: 290px;
  margin: 0 auto;
  padding: 0 15px;

  @media screen and (max-width: $smTabletWidth) {
    padding: 0 16px;
  }
}
