.main-slider {
    
    .container-max {
        padding: 0;
    }
    
    .container {
        position: relative;
    }
    
    &__link {
        text-decoration: none;
    }
    
    &__wrap {
        height: 517px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
    }
    
    &__inner {
        position: absolute;
        top: 0;
        bottom: 0;
    }
    
    &__title {
        width: 60%;
        margin-top: 80px;
        margin-bottom: 45px;
        color: #fff;
        font-weight: bold;
        font-size: 64px;
        line-height: 1.4;
        
        span {
            display: inline-block;
            padding: 0 15px;
            background-image: linear-gradient($color, $color);
            background-size: 100% 81px;
            background-repeat: no-repeat;
            background-position: center bottom;
        }
    }
}