.contacts {
    &__map {
        position: relative;
        padding-bottom: 40.2%;
        overflow: hidden;
    }
    
    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: none;
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .contacts {
        &__map {
            margin: 0 -16px;
        }
    }
}

@media screen and (max-width: $phoneWidth) {
    .contacts {
        &__map {
            padding-bottom: 166%;
        }
    }
}

