.title {
  display: flex;
  justify-content: center;
  
  &__tag {
    display: inline-block;
    padding-bottom: 25px;
    margin: 15px auto 40px;
    font-size: 45px;
    line-height: 1.1;
    font-weight: bold;
    position: relative;
  
    &::before {
      content: "";
      width: 100%;
      height: 1px;
      background-color: $primary;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
    }
  
    &::after {
      content: "";
      width: 68px;
      height: 11px;
      background-color: $primary;
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }
  
  &--white{
    color: #FFF;
  }
}

@media screen and (max-width: $smDesktopWidth) {
  .title {
    &__tag {
      font-size: 24px;
      padding-bottom: 22px;
      
      &::after {
        content: "";
        width: 68px;
        height: 5px;
      }
    }
  }
}

@media screen and (max-width: $tabletWidth) {
  .title {
    &__tag {
      &::before {
        width: 144px;
        right: auto;
      }
    }
  }
}
