.nav {
    font-size: 13px;
    text-transform: uppercase;
    border-bottom: 1px solid $prePrimary;
    
    &__list {
        margin: 0;
        padding: 0;
        list-style: none;
    }
    
    &__item {
        &.active  {
            padding: 15px 23px;
            color: $primary;
            font-weight: bold;
            position: relative;
            
            &::before {
                content: "";
                height: 3px;
                background-color: $color;
                position: absolute;
                bottom: -2px;
                left: 23px;
                right: 23px;
            }
        }
        
        &:first-of-type {
           a {
               padding-left: 0;
           }
            
            &.active {
                padding-left: 0;
    
                &::before {
                    left: 0;
                }
            }
        }
        
        &:last-of-type {
            a {
                padding-right: 0;
            }
    
            &.active {
                padding-right: 0;
    
                &::before {
                    right: 0;
                }
            }
        }
    }
    
    &__link {
        display: block;
        padding: 15px 23px;
        color: $color;
        font-weight: bold;
        text-decoration: none;
        transition: opacity .4s;
        
        &:hover {
            opacity: .75;
        }
    }
}

@media screen and (max-width: $bigTbWidth) {
    .nav {
        &__item {
            &.active {
                padding: 10px 20px;
    
                &::before {
                    left: 20px;
                    right: 20px;
                }
            }
        }
        
        &__link {
            padding: 10px 20px;
        }
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .nav {
        font-size: 11px;
        
        &__item {
            &.active {
                padding: 5px 10px;
    
                &::before {
                    left: 10px;
                    right: 10px;
                }
            }
        }
        
        &__link {
            padding: 5px 10px;
        }
    }
}