.article-content {
    padding: 40px 0;
    
    h2 {
        margin: 32px 0 0;
        font-size: 20px;
        
        + p,
        + ul,
        + ol {
            margin: 16px 0 32px;
        }
    }
    
    h3 {
        margin: 32px 0 0;
        font-size: 17px;
    
        + p,
        + ul,
        + ol {
            margin: 16px 0 32px;
        }
    }
    
    p {
        margin: 32px 0;
    }
    
    ul {
        margin: 32px 0;
        padding: 0;
        list-style: none;
        
        li {
            padding-left: 50px;
            position: relative;
            
            &::before {
                content: "";
                width: 7px;
                height: 7px;
                background-color: $color;
                position: absolute;
                top: 12px;
                left: 18px;
            }
        }
        
    }
    
    ol {
        margin: 32px 0;
    }
    
    li {
        margin: 3px 0;
    }
    
    a {
        color: $color;
        font-size: inherit;
        text-decoration: underline;
        transition: color .4s;
    
        &:hover,
        &:focus-visible {
            color: $colorHover;
        }
    }
    
}

@media screen and (max-width: $tabletWidth) {
    .article-content {
        padding: 30px 0;
    }
}

@media screen and (max-width: $phoneWidth) {
    .article-content {
        padding: 20px 0;
    
        h2 {
            margin: 20px 0 0;
            font-size: 18px;
        
            + p,
            + ul,
            + ol {
                margin: 10px 0 20px;
            }
        }
    
        h3 {
            margin: 20px 0 0;
            font-size: 16px;
        
            + p,
            + ul,
            + ol {
                margin: 10px 0 20px;
            }
        }
    
        p {
            margin: 20px 0;
        }
    
        ul {
            margin: 20px 0;
        
            li {
                padding-left: 50px;
            
                &::before {
                    width: 5px;
                    height: 5px;
                }
            }
        
        }
    
        ol {
            margin: 20px 0;
        }
  
    }
}