.video {
    display: block;
    position: relative;
    overflow: hidden;
    outline: none;
    padding-top: 56%;
    transition: opacity .4s;
    
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
    }
    
    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 10;
        background-color: rgba(33, 57, 110, .3);
        transition: background-color .4s;
    }
    
    &__btn {
        display: inline-block;
        width: 100px;
        height: 100px;
        color: #fff;
        font-size: 20px;
        font-weight: 700;
        background-color: rgba(0,0,0,0);
        border: none;
        letter-spacing: -.02em;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        cursor: pointer;
        outline: none;
        transition: color .4s;
        
        &::before {
            content: "";
            width: 100%;
            height: 100%;
            background-color: #fff;
            background-image: url(../img/assets/icon-play.svg);
            background-repeat: no-repeat;
            background-position: 50%;
            background-size: 26px;
            border-radius: 50%;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            transition: opacity .4s;
        }
    }
    
    &:hover {
        .video__overlay {
            background-color: rgba(33, 57, 110, .1);
        }
        
        .video__btn {
            &::before {
                opacity: .7;
            }
        }
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .video {
        &__btn {
            width: 70px;
            height: 70px;
    
            &::before {
                background-size: 20px;
            }
        }
    }
}

@media screen and (max-width: $phoneWidth) {
    .video {
        &__btn {
            width: 43px;
            height: 43px;
        
            &::before {
                background-size: 14px;
            }
        }
    }
}