.burger {
  display: none;
  width: 40px;
  height: 40px;
  margin-left: 12px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  position: relative;
  right: -5px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &::before {
    width: 40px;
    height: 40px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    border-radius: 50%;
  }

  &::after {
    width: 20px;
    height: 1px;
    background-color: $color;
    border-radius: 3px;
    box-shadow: 0 5px 0 0 $color;
    top: 44%;
  }

}

@media screen and (max-width: $mdTabletWidth) {
  .burger {
    display: block;
  }
}