.article-cart {
    margin: 10px 0;
    background-color: $back;
    text-align: center;
    
    &__link {
        display: block;
        padding: 20px 30px 30px 30px;
        color: $color;
        text-decoration: none;
    }
    
    &__title {
        max-width: 750px;
        margin: 0 auto 20px;
        font-size: 25px;
    }
    
    &__button {
        padding: 12px 40px;
    }
}

@media screen and (max-width: $tabletWidth) {
    .article-cart {
        margin: 15px -16px;
    
        &__link {
            padding: 25px 30px;
        }
    
        &__title {
            font-size: 17px;
        }
    }
}