.contacts-info {
    background-color: $color;
    
    &__wrap {
        padding: 35px 50px;
        margin: 0 35px;
        background-color: #FFF;
        position: relative;
        top: -35px;
        z-index: 100;
    }
    
    &__list {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: nowrap;
    }
    
    &__item {
        padding-left: 35px;
        background-repeat: no-repeat;
        background-position: top 4px left;
        
        &--address {
            background-image: url("../img/assets/icon-pin.svg");
            background-size: 20px;
        }
    }
    
    b, span {
        display: block;
    }
    
    b {
        margin-bottom: 10px;
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .contacts-info {
        height: 170px;
    
        &__wrap {
            padding: 32px 25px 25px;
            margin: 0 5px;
            top: -85px;
        }
        
        b {
            display: none;
        }
        
        &__list {
            display: block;
        }
    
        &__item {
            margin: 5px 0;
            
            &--tel {
                background-image: url("../img/assets/icon-tel.svg");
                background-size: 16px;
            }
    
            &--email {
                background-image: url("../img/assets/icon-mail.svg");
                background-size: 16px;
            }
            
            &:last-of-type {
                padding-left: 0;
                margin-top: 25px;
                text-align: center;
            }
        }
    }
}