.header-content {
    padding: 14px 0 25px;
    
    &__wrap {
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
    
    &__logo {
        padding-right: 5.3%;
    }
    
    &__nav {
        margin-right: auto;
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .header-content {
        &__logo {
            padding-right: 2%;
        }
    
        &__nav {
            margin-right: 0;
        }
        
        &__wrap {
            justify-content: space-between;
        }
    
        &__button {
            padding: 12px 10px;
            font-size: 14px;
            white-space: nowrap;
        }
    }
}

@media screen and (max-width: $mdTabletWidth) {
    .header-content {
        &__logo {
            margin-right: auto;
        }
        
        &__nav {
            display: none;
        }
    
        &__wrap {
            justify-content: flex-start;
        }
    }
}
