.video-block {
    padding: 30px 0 90px;
    
    &__wrap {
        display: grid;
        grid-template-columns: 3fr 1fr;
        grid-gap: 30px;
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .video-block {
        padding: 30px 0 0;
    
        &__wrap {
            margin: 0 -15px;
            display: block;
        }
    }
}

@media screen and (max-width: $phoneWidth) {
    .video-block {
        padding: 10px 0 0;
        
        &__wrap {
            margin: 0 -16px;
        }
    }
}
