.footer {
  font-size: 13px;
  
  &__wrap {
    flex-grow: 1;
  }
  
  &__year {
    font-size: 15px;
    margin-left: auto;
    margin-right: 8.1%;
  }
  
  &__bottom {
    padding: 20px 0 50px;
  }
  
  &__nav {
    padding: 24px 0;
    font-size: 17px;
    text-transform: none;
  }
}

@media screen and (max-width: $tabletWidth ) {
  .footer {
    padding: 30px 0 15px;
    font-size: 12px;
    
    &__bottom {
      display: block;
      padding: 15px 0 0;
    }
    
    &__wrap {
      margin-top: 20px;
    }
    
    &__year {
      margin-left: 0;
    }
    
    &__nav {
      padding: 0;
      border-bottom: none;
  
      .nav__list {
      
      }
      .nav__item {
        min-width: 34%;
      }
      
      .nav__link {
        padding: 0;
        font-size: 15px;
      }
    }
  }
}
