.project-input {
  font: inherit;
  width: 100%;
  height: 40px;
  padding: 10px 15px;
  background: transparent;
  border: 1px solid $prePrimary;
  border-radius: 0;
  box-shadow: none;
  transition: border-color .4s;
  outline: none;

  &::placeholder {
    color: $prePrimary;
    transition: color .4s;
  }
  
  &:disabled {
    opacity: 0.7;
  }

  &:not(:disabled):hover,
  &:not(:disabled):focus-visible {
    border-color: $color;

    &::placeholder {
      color: $color;
    }
  }
}