.features {
    background-color: $color;
    
    &__container {
        min-height: 330px;
        position: relative;
    }
    
    &__wrap {
        position: absolute;
        left: 0;
        right: 0;
        top: -80px;
        display: grid;
        grid-template-columns: repeat(3, minmax(280px, 1fr));
        grid-column-gap: 0;
        grid-row-gap: 20px;
    }
}