.header-line {
    
    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 14px 0 13px calc(260px + 5.3%);
        border-bottom: 1px solid $prePrimary;
    }
}

@media screen and (max-width: $bigTbWidth) {
    .header-line {
        &__wrap {
            padding: 14px 0 13px calc(150px + 5.3%);
        }
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .header-line {
        display: none;
    }
}
