.tel {
    &__list {
        margin: 0;
        padding: 0;
        list-style: none;
    }
    
    &__item {
        margin-right: 30px;
        
        &:last-of-type {
            margin-right: 0;
        }
    }
    
    &__link {
        color: $color;
        font-size: 20px;
        font-weight: bold;
        text-decoration: none;
        transition: opacity .4s;
        
        &:hover,
        &:focus-visible {
            opacity: .75;
        }
    }
    
}