.tab {
    &__btn {
        width: 100%;
        padding: 20px 56px 20px 0;
        color: $color;
        font: inherit;
        font-size: 20px;
        font-weight: bold;
        text-align: left;
        background-color:transparent;
        border: none;
        border-bottom: 1px solid $prePrimary;
        box-shadow: none;
        cursor: pointer;
        transition: opacity .4s, border-bottom .4s;
        position: relative;
        
        &::after {
            content: "";
            width: 20px;
            height: 20px;
            background-repeat: no-repeat;
            background-position: center;
            background-image: url("../img/assets/arrow-down.svg");
            background-size: 18px;
            position: absolute;
            top: 50%;
            right: 17px;
            transform: translateY(-50%);
            z-index: 2;
            transition: transform .4s;
        }
        
        &:hover,
        &:focus-visible {
            opacity: .75;
        }
    }
    
    &__content {
        padding: 15px 0;
        display: none;
        
        p {
            margin: 10px 0;
        }
    }
    
    .opened {
        border-bottom: 1px solid transparent;
        
        &::after {
            transform: translateY(-50%) rotate(180deg);
        }
        
        + .tab__content {
            border-bottom: 1px solid $prePrimary;
            transition: border-bottom .4s;
        }
    }
    
    &:last-of-type {
        .tab__btn,
        .tab__content {
            border-bottom: 1px solid transparent;
        }
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .tab {
        &__btn {
            padding: 15px 44px 15px 0;
            font-size: 17px;
    
            &::after {
                width: 15px;
                height: 15px;
                background-size: 13px;
                right: 17px;
            }
        }
    
        &__content {
            padding: 10px 0;
        }
    }
}