.button-top {
    display: block;
    width: 45px;
    height: 45px;
    padding: 0;
    margin: 0;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-image: url("../img/assets/icon-arrow.svg");
    border: 1px solid transparent;
    border-radius: 50%;
    box-shadow: none;
    cursor: pointer;
    transition: opacity .4s;
    position: fixed;
    bottom: 100px;
    right: 8%;
    z-index: 50;
    
    &:hover,
    &:focus-visible {
        opacity: .75;
    }
}

@media screen and (max-width: $tabletWidth) {
    .button-top {
        display: none;
    }
}