.service-card {
    text-align: center;
    position: relative;
    
    &__link {
        color: $color;
        text-decoration: none;
    
        &:hover {
            .service-card__hover {
                visibility: visible;
                opacity: 1;
            }
    
            .service-card__bottom {
                visibility: hidden;
                opacity: 0;
            }
        }
    }
    
    &__image-wrap {
        width: 100%;
        padding: 50%;
        position: relative;
        overflow: hidden;
    }
    
    &__img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__bottom {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba(255, 255, 255, .8);
        padding: 5px 15px;
        opacity: 1;
        transition: opacity .4s;
    }
    
    &__title {
        font-size: 25px;
    }
    
    &__hover {
        padding: 5px;
        visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(255, 255, 255, .8);
        opacity: 0;
        transition: opacity .4s, background-color .4s;
    }
    
}