.head {
    background-color: $back;
    .container-max {
        padding: 0;
    }
    
    &__wrap {
        min-height: 395px;
        padding: 60px 50px;
        background-repeat: no-repeat;
        background-position: center right;
        background-size: contain;
        position: relative;
        
        &::before {
            content: "";
            width: 100%;
            height: 100%;
            background: linear-gradient(89.63deg, #F5F9FC 38.54%, rgba(255, 255, 255, 0) 59.32%);
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
        }
    }
    
    &__inner {
        padding: 0 40px;
        max-width: 415px;
        position: relative;
        z-index: 2;
    }
    
    &__type {
        font-size: 13px;
        font-weight: bold;
        text-transform: uppercase;
    }
    
    &__author {
        margin: 20px 0;
    }
    
    &__link {
        font-size: inherit;
    }
    
    &__descr {
        display: block;
    }
    
    .title__tag {
        margin-left: 0;
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .head {
        &__wrap {
            min-height: 280px;
            padding: 40px 20px;
        }
        
        &__inner {
            max-width: 370px;
        }
        
        &__descr {
            margin-top: 10px;
        }
    }
}

@media screen and (max-width: $tabletWidth) {
    .head {
        &__wrap {
            min-height: 407px;
            padding: 30px 0;
            background-size: cover;
            background-position: center;
    
            &::before {
                background: linear-gradient(143deg, #F5F9FC 50.54%, rgba(255, 255, 255, 0) 61.32%);
            }
        }
        
        &__inner {
            padding: 0;
            max-width: 200px;
        }
    
        &__descr {
            margin-top: -15px;
        }
    }
}

@media screen and (max-width: $phoneWidth) {
    .head {
        &__link {
            display: block;
        }
    }
}