.feature {
    background-color: #FFF;
    padding: 30px 30px 30px 50px;
    border-right: 1px solid #8298AB;
    
    &:last-of-type {
        border-right: none;
    }
    
    &__link {
        color: $color;
        text-decoration: none;
    }
    
    &__name {
        color: #8298AB;
        text-transform: uppercase;
    }
    
    &__title {
        min-height: 64px;
        padding-left: 75px;
        margin: 15px 0;
        font-size: 25px;
        line-height: 1.2;
        background-repeat: no-repeat;
        background-position: center left;
        background-size: 63px;
        
        &--research {
            background-image: url("../img/assets/feature-1.svg");
        }
        
        &--zoo {
            background-image: url("../img/assets/feature-2.svg");
        }
        
        &--insurance {
            background-image: url("../img/assets/feature-3.svg");
        }
    }
    
    &__description {
        margin: 0 0 35px;
        text-transform: lowercase;
    }
}