.pet-form {
    &__wrap {
        padding: 0 61px 0 99px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 110px;
    }
    
    &__col {
        &--gradient {
            padding: 105px 45px;
            background: linear-gradient(180deg, $primary 53%, $color 53%);
            position: relative;
            
            &::before {
                content: "";
                width: 230px;
                height: 367px;
                background-image: url("../img/assets/back-dog.png");
                background-repeat: no-repeat;
                background-size: cover;
                position: absolute;
                bottom: 0;
                left: -196px;
                z-index: 1;
            }
        }
    }
    
    &__title {
        display: inline-block;
        padding-bottom: 20px;
        margin: 15px auto 40px;
        font-size: 45px;
        font-weight: bold;
        position: relative;
    
        &::before {
            content: "";
            width: 65%;
            height: 1px;
            background-color: $primary;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
        }
    
        &::after {
            content: "";
            width: 68px;
            height: 11px;
            background-color: $primary;
            position: absolute;
            left: 0;
            bottom: 0;
        }
    }
    
    &__text {
        width: 68%;
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .pet-form {
        &__wrap {
            padding: 0 0 0 60px;
            grid-column-gap: 15px;
        }
        
        &__title {
            font-size: 32px;
            line-height: 1.2;
            
            &::after {
                content: "";
                width: 68px;
                height: 5px;
            }
        }
        
        &__col {
            &--gradient {
                padding: 55px 25px;
                margin-right: -15px;
            }
        }
    }
}

@media screen and (max-width: $tabletWidth) {
    .pet-form {
        &__title {
            font-size: 24px;
        }
    
        &__col {
            &--gradient {
                &::before {
                    height: 240px;
                    left: -111px;
                    background-size: contain;
                }
            }
        }
    }
}

@media screen and (max-width: $smTabletWidth) {
    .pet-form {
        padding-top: 35px;
        background-color: $back;
        
        &__wrap {
            display: block;
            padding-left: 0;
        }
        
        &__title {
            margin: 20px 0;
            
            &::after {
                content: "";
                width: 68px;
                height: 5px;
            }
        }
    
        &__text {
            margin-bottom: 40px;
            width: 100%;
        }
        
        &__col {
            padding: 0 15px 0 30px;
            
            &--gradient {
                margin: 0 -16px;
                padding: 40px 20px 70px;
                
                &::before {
                    width: 154px;
                    height: 220px;
                    left: auto;
                    right: 0;
                    background-size: contain;
                }
            }
        }
    }
}

