.breadcrumbs {
  padding: 30px 0;
  
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    padding-right: 32px;
    color: $color;
    opacity: 0.75;
    font-size: 14px;
    position: relative;

    &::before {
      content: "";
      width: 14px;
      height: 20px;
      background-image: url("../img/assets/icon-breadcrumbs-arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      position: absolute;
      top: 2px;
      right: 10px;
    }

    &:last-of-type {
      &::before {
        display: none;
      }
    }
  }

  &__link {
    color: $prePrimary;
    text-decoration: none;
    font-size: 14px;
  }
}

@media screen and (max-width: $phoneWidth) {
  .breadcrumbs {
    padding: 5px 0 20px;
    
    &__item {
      padding-right: 30px;
      font-size: 12px;
  
      &::before {
        background-size: 8px;
        top: 1px;
        right: 8px;
      }
    }
  
    &__link {
      font-size: 12px;
    }
  }
}