.error-block {
    background-color: $back;
    
    &__wrap {
        max-width: 720px;
        padding: 90px 0;
        margin: 0 auto;
        background-image: url("../img/assets/back-error.png");
        background-repeat: no-repeat;
        background-position: bottom right 20px;
        background-size: 280px;
    }
    
    &__content {
        max-width: 275px;
        text-align: center;
    }
    
    &__title {
        color: $primary;
        font-size: 45px;
        margin: 20px 0;
    }
    
    &__descr {
        margin: 16px 0 24px;
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .error-block {
        &__wrap {
            max-width: 70%;
            padding: 60px 0;
            background-size: 200px;
        }
        
        &__title {
            font-size: 30px;
        }
    }
}

@media screen and (max-width: $phoneWidth) {
    .error-block {
        
        &__content {
            margin: 0 auto;
        }
        
        &__wrap {
            max-width: 100%;
            padding: 30px 0 305px;
            background-size: 230px;
            background-position: bottom center;
        }
    
        &__descr {
            margin: 14px 0 20px;
        }
    }
}