.shares {
    &__cards {
        padding-top: 90px;
        padding-bottom: 30px;
        display: grid;
        grid-template-columns: repeat(2, minmax(120px, 1fr));
        grid-gap: 30px;
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .shares {
        &__cards {
            padding-top: 70px;
            padding-bottom: 20px;
            grid-gap: 20px;
        }
    }
}

@media screen and (max-width: $smTabletWidth) {
    .shares {
        &__cards {
            padding: 20px 0;
            margin: 0 -16px;
            display: block;
        }
    }
}