.form {
    padding: 40px 39px 40px 31px;
    background-color: #FFF;
    
    &__title {
        display: block;
        margin: 20px 0;
        font-size: 20px;
    }
    
    &__clarification {
        display: block;
        margin-bottom: 20px;
        font-size: 13px;
    }
}

@media screen and (max-width: $smDesktopWidth) {
    .form {
        padding: 30px 26px 32px 30px;
    }
}

@media screen and (max-width: $smTabletWidth) {
    .form {
        
        &__title {
            margin-top: 0;
            font-size: 18px;
        }
        
        &__clarification {
            max-width: 140px;
        }
    }
}

@media screen and (max-width: 340px) {
    .form {
        &__clarification {
            max-width: 135px;
            font-size: 12px;
        }
    }
}