.button {
  display: inline-block;
  min-height: 40px;
  padding: 10px 20px;
  font: inherit;
  color: #FFF;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  line-height: 1;
  overflow-wrap: normal;
  word-wrap: normal;
  background-color: $primary;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  outline: none;
  transition: background-color .4s, opacity .4s;
  
    &--large {
      min-height: 60px;
      padding: 18px 25px;
    }
  
    &:hover,
    &:focus-visible {
      background-color: $primaryHover;
    }
  
    &:active {
      background-color: $color;
      opacity: 0.5;
    }
    
    &:disabled {
      background-color: $prePrimary;
      opacity: 0.5;
    }
    
  }

@media screen and (max-width: $smDesktopWidth) {
  .button {
    padding: 12px 20px;
    font-size: 17px;
  }
}

