.pagination {
    padding: 30px 0 55px;
    
    &__wrap {
        display: flex;
    }
    
    &__btn {
        display: block;
        width: 40px;
        height: 40px;
        background-repeat: no-repeat;
        background-position: center;
        transition: opacity .4s;
        
        &--prev {
            background-image: url("../img/assets/icon-arrow-left.svg");
        }
        
        &--next {
            background-image: url("../img/assets/icon-arrow-right.svg");
        }
        
        &.disabled {
            opacity: .5;
            pointer-events: none;
        }
        
        &:hover,
        &:focus-visible {
            opacity: .75;
        }
    }
}

@media screen and (max-width: $phoneWidth) {
    .pagination {
        padding: 20px 0 40px;
    }
}