.advantages {
    padding: 90px 0 110px;
    
    &__wrap {
        margin-bottom: 45px;
    }
    
    &__list {
        margin: 0;
        padding: 0;
        list-style: none;
        display: grid;
        grid-template-columns: repeat(3, minmax(120px, 1fr));
        grid-column-gap: 20px;
        grid-row-gap: 20px;
    }
    
    &__item {
        padding-left: 70px;
        background-repeat: no-repeat;
        background-position: top left;
        
        
        &--vet {
            background-image: url("../img/assets/advantage-1.svg");
        }
    
        &--hospitals {
            background-image: url("../img/assets/advantage-2.svg");
        }
    
        &--diagnose {
            background-image: url("../img/assets/advantage-3.svg");
        }
    
        &--around {
            background-image: url("../img/assets/advantage-4.svg");
        }
    
        &--outdoor {
            background-image: url("../img/assets/advantage-5.svg");
        }
    
        &--therapy {
            background-image: url("../img/assets/advantage-6.svg");
        }
    
        &--service {
            background-image: url("../img/assets/advantage-7.svg");
        }
    
        &--taxi {
            background-image: url("../img/assets/advantage-8.svg");
        }
    
        &--hotels {
            background-image: url("../img/assets/advantage-9.svg");
        }
    }
    
    &__title {
        display: block;
        margin-bottom: 10px;
        font-size: 20px;
    }
    
    &__descr {
        margin: 0;
    }
}