.field-wrap {
  margin: 10px 0;
  position: relative;

  span {
    font-size: 12px;
    color: $error;
    position: absolute;
    opacity: 0;
    bottom: -10px;
    left: 0;
  }

  &.success {
    position: relative;

    .project-input {
      border-color: $success;
      padding-right: 43px;
    }

    &::before {
      content: "";
      width: 43px;
      height: 43px;
      background-image: url("../img/assets/icon-success.svg");
      background-repeat: no-repeat;
      background-position: center;
      position: absolute;
      bottom: 0;
      right: 0;
    }
  }

  &.error {
    position: relative;

    .project-input {
      border-color: $error;
      padding-right: 43px;
    }

    &::before {
      content: "";
      width: 43px;
      height: 43px;
      background-image: url("../img/assets/icon-error.svg");
      background-repeat: no-repeat;
      background-position: center;
      position: absolute;
      bottom: 0;
      right: 0;
    }

    span {
      opacity: 1;
    }
  }
}